@import "@radix-ui/colors/black-alpha.css";
@import "@radix-ui/colors/green.css";
@import "@radix-ui/colors/mauve.css";
@import "@radix-ui/colors/violet.css";

.sizeChart {
  background: transparent;
  color: #7d9399;
  text-decoration: underline;
  text-underline-offset: 3px;
}

.sizeChart:hover {
  cursor: pointer;
}

.dialogOverlay {
  background-color: var(--black-a9);
  position: fixed;
  inset: 0;
  animation: overlayShow 150ms cubic-bezier(0.16, 1, 0.3, 1);
}

.dialogTitle {
  text-align: center;
}

.dialogDescription {
  padding: 20px 0;
}

.dialogContent {
  background-color: white;
  border-radius: 6px;
  box-shadow: hsl(206 22% 7% / 35%) 0px 10px 38px -10px,
    hsl(206 22% 7% / 20%) 0px 10px 20px -15px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90vw;
  max-width: 450px;
  max-height: 85vh;
  padding: 25px;
  animation: contentShow 150ms cubic-bezier(0.16, 1, 0.3, 1);
}

.dialogContent:focus {
  outline: none;
}

.wrapperTable {
  width: 100%;
}

.wrapperTable td {
  text-align: center;
  padding: 2px;
}

.closeDialog {
  font-family: inherit;
  border-radius: 100%;
  height: 25px;
  width: 25px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--violet-11);
  position: absolute;
  top: 10px;
  right: 10px;
}

.closeDialog:hover {
  background-color: var(--violet-4);
  cursor: pointer;
}

.closeDialog:focus {
  box-shadow: 0 0 0 2px var(--violet-7);
}
